
.h-container {
  margin: 0 4%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.h-tagline {
  padding: 0 0 0 3%;
}

.h-nav-btns-container {
  padding: 40px 0 0 0;
  display: flex;
  justify-content: flex-end;
}

.h-nav-btn {
  margin: 0 0 0 30px;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  padding-bottom: 20px;
  border-bottom: 3px solid transparent;
  transition: 0.9s;
  color: #fff;
}

.h-nav-btn:hover {
  border-bottom: 3px solid rgba(234, 202, 187);
  cursor: pointer;
}

.h-main-icon {
  margin-top: 10px;
  height: 80px;
  width: 80px;
}

.h-main-icon:hover {
  margin-top: 10px;
  height: 80px;
  width: 80px;
}
.f-container {
  background-color: #EACABB;
  margin: 0;
  height: 150px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.f-social-icon {
  height: 25px;
  width: 25px;
  margin: 0 10px;
}
.i-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -96px;
  height: 100vh;
  background-image: url(/static/media/bananatree.7eb99ad0.jpeg);
  background-size: cover;
}

.i-title {
  padding: 300px 0 0;
  margin: 0;
  font-size: 4rem;
  text-transform: uppercase;
  letter-spacing: 15px;
  color: #fff;
  font-weight: normal;
}

.i-break {
  width: 10%;
  border-bottom: 3px solid rgba(234, 202, 187);
  height: 30px;
}

.i-tagline {
  letter-spacing: 4px;
  padding: 0 0 200px;
  color: #fff;
  font-weight: normal;
}
#about-me {
  border-top: 15px solid rgba(234, 202, 187);
  display: flex;
  align-items: center;
  padding: 100px 15%;
  justify-content: flex-end;
}

.am-text-container {
  width: 60%;
  text-align: right;
  margin-right: 100px;
}

.am-title {

}

.am-text {

}

.am-me-icon {
  border-radius: 50%;
  height: 300px;
  width: 300px;
  background-image: url(/static/media/pic.73c90fa0.jpg);
  background-size: cover;
}
.s-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 50px 5% 80px;
  background:
    linear-gradient(
      rgba(255, 255, 255, .8),
      rgba(255, 255, 255, .8)),
    url(/static/media/paint.80a2858a.jpeg);
  background-size: cover;
}

.s-skills {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(205px, 1fr));
}

.s-skill {
  border-top: 15px solid #eacabb;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px 20px 10px;
}

.s-skill-item {
  margin: 5px 0;
}

.s-title {
  margin: 0 0 20px;
  letter-spacing: 5px;
}

.s-subheading {
  letter-spacing: 5px;
  margin: 20px 0 0;
}

.s-icon {
  width: 80px;
  height: 80px;
  margin: 20px 0 40px;
}
.pp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.pp-projects-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  width: 100%;
}

.pp-project-container {
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1%;
}

.pp-title {
  color: #fff;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: normal;
}

.pp-icon {
  width: 40px;
  height: 40px;
  margin: 40px;
}

.pp-icon:hover {
  cursor: pointer;
}


.p-container {
  display: flex;
  flex-direction: column;
  width: 1200px;
  height: 700px;
  justify-content: center;
  align-items: center;
}

.p-title {
  text-transform: uppercase;
  text-align: center;
  margin: 0 0 40px;
  letter-spacing: 7px;

}

.p-project-img {
  width: 400px;
  margin: 0 auto;
}

.p-text {
  text-align: center;
  width: 70%;
  margin: 30px auto;
}

.p-tech-icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 80%;
}

.p-tech-icon {
  height: 30px;
  margin: 10px;
}

.p-btns {
  display: flex;
  justify-content: center;
  width: 60%;
}

.p-btn {
  background: rgba(222, 172, 148, .7);
  color: #FFF;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: .6rem;
  margin: 2px;
  transition: .5s;
  width: 30%;
  height: 20px;
}

.p-btn:hover {
  background: rgba(222, 172, 148);
}

.p-left-0 {
  -webkit-animation: 1s ease-out 0s 1 slideInFromLeftA;
          animation: 1s ease-out 0s 1 slideInFromLeftA;
}

.p-right-0 {
  -webkit-animation: 1s ease-out 0s 1 slideInFromRightA;
          animation: 1s ease-out 0s 1 slideInFromRightA;
}

.p-left-1 {
  -webkit-animation: 1s ease-out 0s 1 slideInFromLeftB;
          animation: 1s ease-out 0s 1 slideInFromLeftB;
}

.p-right-1 {
