}

.p-left-4 {
  -webkit-animation: 1s ease-out 0s 1 slideInFromLeftE;
          animation: 1s ease-out 0s 1 slideInFromLeftE;
}

.p-right-4 {
  -webkit-animation: 1s ease-out 0s 1 slideInFromRightE;
          animation: 1s ease-out 0s 1 slideInFromRightE;
}
  
@-webkit-keyframes slideInFromLeftA {
  0% {
    -webkit-transform: translate(-100%);
            transform: translate(-100%);
  }
  100% {
    -webkit-transform: translate(0);
            transform: translate(0);
  }

}
  
@keyframes slideInFromLeftA {
  0% {
    -webkit-transform: translate(-100%);
            transform: translate(-100%);
  }
  100% {
    -webkit-transform: translate(0);
            transform: translate(0);
  }

}@-webkit-keyframes slideInFromRightA {
  0% {
    -webkit-transform: translate(100%);
            transform: translate(100%);
  }
  -100% {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}@keyframes slideInFromRightA {
  0% {
    -webkit-transform: translate(100%);
            transform: translate(100%);
  }
  -100% {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

